.overveiw {
  .modal-title {
    text-align: center;
  }
  .btn.btn-outline-secondary {
    color: #136162;
    border: 1px solid rgba(5, 36, 37, 0.12);
    &:hover {
      border: 1px solid #136162;
      background-color: #fff;
    }
  }
  .titile-name {
    color: #136162;
    font-size: 16px;
    font-weight: 500;
  }
  .building-card-holder {
    border-radius: 12px;
    overflow: hidden;
    border: 1px solid rgba(5, 36, 37, 0.12);
    &:hover {
      background: #b4dbdb;
      //   background: pink;
      // cursor: pointer;
    }
    &:focus {
      background: #b4dbdb;
      //   background: pink;
      // cursor: pointer;
    }
    &:active {
      background: #b4dbdb;
      // cursor: pointer;
    }
    .building-card-footer {
      h6 {
        font-size: 14px;
        text-transform: uppercase;
        color: #136162;
      }
    }
  }
  .building-image {
    position: relative;
    .building-caption {
      position: absolute;
      top: 0;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.4) 40.1%, rgba(0, 0, 0, 0.5) 57.29%, rgba(0, 0, 0, 0.6) 72.92%);
      width: 100%;
      height: 100%;
      display: flex;
      align-items: flex-end;
      .caption-holder {
        width: 100%;
        color: #fff;
        h3 {
          font-weight: normal;
          font-size: 34px;
        }
      }
      .building-details-holder {
        color: rgba(255, 255, 255, 0.74);
      }
    }
  }
  .main-content-tabs-section {
    height: 100vh;
    .nav-tabs {
      .nav-link {
        text-transform: uppercase;
        flex: auto;
        font-size: 14px;
        color: rgba(5, 36, 37, 0.6);
        padding: 0.8rem 0.5rem;
        font-weight: 500;
        &:hover,
        &.active {
          color: #136162;
          border-bottom: 2px solid;
          border-color: #fff #fff #136162;
        }
      }
    }
    .tab-content {
      // height: calc(100vh - 22vh);
      .tab-pane {
        height: inherit;
        &.people-tab-holder {
          .tabs-siderbar {
            .tab-pane {
              ul {
                .list-item {
                  border-bottom: 1px solid rgba(5, 36, 37, 0.12);
                  padding: 11px 15px;
                  color: rgba(5, 36, 37, 0.87);
                  //   &:active,
                  //   &:hover {
                  //     background: #b4dbdb;
                  //     cursor: pointer;
                  //   }
                  &:hover {
                    background: #b4dbdb;
                    //   background: pink;
                    cursor: pointer;
                  }
                  &:focus {
                    background: #b4dbdb;
                    //   background: pink;
                    cursor: pointer;
                  }
                  &:active {
                    background: #b4dbdb;
                    cursor: pointer;
                  }
                  //   &:focus-visible {
                  //     background: #b4dbdb;
                  //     cursor: pointer;
                  //   }
                  //   &:focus-within {
                  //     background: #b4dbdb;
                  //     cursor: pointer;
                  //   }
                  //   &:target {
                  //     background: #b4dbdb;
                  //     cursor: pointer;
                  //   }
                }
              }
            }
          }
        }
      }
      .tabs-inner-content-holder {
        display: flex;
        height: inherit;
        .tabs-siderbar {
          max-width: 400px;
          width: 100%;
          padding: 15px;
          border-right: 1px solid rgba(5, 36, 37, 0.12);
        }
        .tabs-inner-block {
          width: 100%;
          .section-details {
            height: 100%;
            .section-details-header {
              .section-header {
                h5 {
                  color: rgba(5, 36, 37, 0.6);
                  letter-spacing: 0.15px;
                  font-weight: 500;
                  margin: 0;
                }
              }
            }
            .report-header {
              display: flex;
              justify-content: space-between;
              padding: 11px 15px 5px;
              .list-content,
              .list-label {
                font-size: 14px;
                text-transform: uppercase;
                color: rgba(5, 36, 37, 0.38);
                font-weight: 500;
              }
            }
          }
          .people-details-holder,
          .inspections-details {
            padding: 15px;
            max-height: calc(100% - 4em);
            overflow: scroll;
            .header {
              margin: 15px 0;
              color: rgba(5, 36, 37, 0.6);
              span {
                color: rgba(5, 36, 37, 0.87);
              }
            }
            .accordion {
              .accordion-item {
                border: 1px solid rgba(5, 36, 37, 0.12);
                border-radius: 12px;
                .accordion-header {
                  .accordion-button {
                    border: 0;
                    &:focus {
                      box-shadow: none;
                    }
                    &:after {
                      margin-top: -50px;
                    }
                    &:not(.collapsed) {
                      color: #212529;
                      background-color: transparent;
                    }
                    .inspections-sub-details {
                      .date-label {
                        font-size: 12px;
                        color: #136162;
                      }
                      h4 {
                        font-weight: 500;
                        color: rgba(5, 36, 37, 0.87);
                        margin: 5px 0 8px;
                        font-size: 20px;
                      }
                      .inspection-name {
                        color: rgba(5, 36, 37, 0.87);
                        font-weight: normal;
                      }
                    }
                    .points-holder {
                      .points-label {
                        font-size: 10px;
                        color: rgba(5, 36, 37, 0.38);
                        letter-spacing: 1.5px;
                        text-transform: uppercase;
                      }
                      .points-value {
                        color: #136162;
                        font-size: 36px;
                      }
                    }
                  }
                }
                .accordion-collapse {
                  border: 0;
                  border-top: 1px solid rgba(5, 36, 37, 0.12);
                  .inspections-table {
                    .header-block {
                      color: rgba(5, 36, 37, 0.38);
                      font-size: 14px;
                      font-weight: 500;
                      .header-label {
                        flex: 1 0 0%;

                        &:nth-child(3) {
                          max-width: 200px;
                          text-align: center;
                        }
                        &:nth-child(2) {
                          max-width: 100px;
                          text-align: center;
                        }
                      }
                    }
                    .content-block {
                      ul {
                        padding: 0;
                        list-style: none;
                        li {
                          .content-text {
                            display: flex;
                            color: rgba(5, 36, 37, 0.87);
                            border-bottom: 1px solid rgba(5, 36, 37, 0.12);
                            .content-parameter {
                              flex: 1 0 0%;
                            }
                            .content-result {
                              flex: 1 0 0%;
                              max-width: 100px;
                              text-align: center;
                              color: rgba(46, 210, 92, 0.87);
                            }
                            .content-points {
                              flex: 1 0 0%;
                              max-width: 200px;
                              text-align: center;
                              font-weight: 500;
                            }
                          }
                          &.fail {
                            .content-result {
                              color: rgba(249, 34, 21, 0.87);
                            }
                            .content-text {
                              border-bottom: 0;
                            }
                          }
                          &:last-child {
                            .content-text {
                              border-bottom: 0;
                            }
                          }
                          .content-inner-block {
                            padding: 0 32px;
                            color: rgba(5, 36, 37, 0.6);
                            font-size: 14px;
                            border-bottom: 1px solid rgba(5, 36, 37, 0.12);
                            padding-bottom: 15px;
                            h6 {
                              color: rgba(5, 36, 37, 0.87);
                              font-size: 14px;
                              font-weight: 500;
                              margin-bottom: 10px;
                            }
                            .image-block {
                              border-radius: 12px;
                              overflow: hidden;
                              margin: 0 10px;
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .section-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      h6 {
        color: rgba(5, 36, 37, 0.6);
        letter-spacing: 0.15px;
        font-weight: 500;
        margin: 0;
      }
      .header-button {
        .btn {
          color: #136162;
          border: 1px solid rgba(5, 36, 37, 0.12);
          &:hover {
            border: 1px solid #136162;
            background-color: #fff;
          }
        }
      }
    }
    .inner-tab-section-list-holder {
      max-height: calc(100% - 12em);
      overflow: scroll;
      .section-list-block {
        list-style: none;

        .inner-tab-section-list-item {
          border-bottom: 1px solid rgba(5, 36, 37, 0.12);
