            cursor: pointer;
          }
          .list-label {
            color: rgba(5, 36, 37, 0.87);
            font-size: 16px;
            font-weight: normal;
          }
          .list-content {
            color: rgba(5, 36, 37, 0.87);
            font-weight: 500;
          }
        }
      }
      &.room-block {
        .inner-tab-section-list-item {
          border-radius: 12px;
          margin-bottom: 15px;
          .list-label {
            color: #1c4f51;
            font-weight: 500;
            margin-bottom: 15px;
          }
          .room-details {
            font-size: 14px;
            color: rgba(5, 36, 37, 0.6);
            margin-bottom: 5px;
            span {
              margin-right: 40px;
            }
          }
        }
      }
    }
